    border-radius: 12px;
    box-sizing: border-box;
    cursor: default;
    height: 24px;
    width: 24px;
    -webkit-appearance: none;
    margin-top: -6px; }
  [type='range']::-moz-range-thumb {
    background: var(--white);
    border: 8px solid var(--primary);
    border-radius: 12px;
    box-sizing: border-box;
    cursor: default;
    height: 24px;
    width: 24px; }
  [type='range']::-ms-track {
